import axios from 'axios'
import { useFormik } from 'formik'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
export default function Register() {
  const [loading, setLoading] = useState(false)
  let baseURL = "https://route-ecommerce.onrender.com"
  let navigate = useNavigate();
  const [errMessage, seterrMessage] = useState("")
  let validationSchema = Yup.object({
    name: Yup.string().required("Name is required").min(3, "minmum name 3 char").max(15, "maxmum name 15 char"),
    email: Yup.string().required("Name is required").email('Email invalid'),
    password: Yup.string().required('Password is required').min(8, 'Password must be at least 8 characters long').matches(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)/, 'Password must contain at least one uppercase letter, one lowercase letter, and one numeric digit'),
    rePassword: Yup.string().required('rePassword is required').oneOf([Yup.ref("password")], "rePassword does`t match"),
    phone: Yup.string().required('Phone number is required').matches(/^(010|011|012|015)\d{8}$/, 'Invalid phone number').max(11, 'Phone number must be at most 11 digits long')
  })

  let formik = useFormik({
    initialValues: {
      name: "",
      email: "",
      password: "",
      rePassword: "",
      phone: ""
    }, 
    onSubmit: (values)=>{
      sendRegisterData(values)
    }, 
    validationSchema
  })
  async function sendRegisterData(userData) {
    setLoading(true)
    let { data } = await axios.post(`${baseURL}/api/v1/auth/signup`, userData).catch((error) => {
      seterrMessage(error.response.data.message)
      setLoading(false)
    })
    console.log(data);
    if (data.message === "success") {
      setLoading(false)
      navigate("/login")
    }
  }
  return <>
    <div className='w-75 mx-auto'>
      <h2>Register Now</h2>
      <form onSubmit={formik.handleSubmit}>

        <label htmlFor="name">Name</label>
        <input type="text" name="name" id="name" onBlur={formik.handleBlur} onChange={formik.handleChange} className="form-control mb-2" value={formik.values.name} />
        {formik.errors.name && formik.touched.name ? <div className='alert alert-danger'>{formik.errors.name}</div> : ""}

        <label htmlFor="email">Email</label>
        <input type="email" name="email" id="email" onBlur={formik.handleBlur} onChange={formik.handleChange} className="form-control mb-2" value={formik.values.email} />
        {formik.errors.email && formik.touched.email ? <div className='alert alert-danger'>{formik.errors.email}</div> : ""}


        <label htmlFor="password">Password</label>
        <input type="password" name="password" id="password" onBlur={formik.handleBlur} onChange={formik.handleChange} className="form-control mb-2" value={formik.values.password} />
        {formik.errors.password && formik.touched.password ? <div className='alert alert-danger'>{formik.errors.password}</div> : ""}


        <label htmlFor="rePassword">rePassword</label>
        <input type="password" name="rePassword" id="rePassword" onBlur={formik.handleBlur} onChange={formik.handleChange} className="form-control mb-2" value={formik.values.rePassword} />
        {formik.errors.rePassword && formik.touched.rePassword ? <div className='alert alert-danger'>{formik.errors.rePassword}</div> : ""}


        <label htmlFor="phone">Phone</label>
        <input type="tel" name="phone" id="phone" onBlur={formik.handleBlur} onChange={formik.handleChange} className="form-control mb-2" value={formik.values.phone} />
        {formik.errors.phone && formik.touched.phone ? <div className='alert alert-danger'>{formik.errors.phone}</div> : ""}
        {errMessage !== "" ? <div className='alert alert-danger'>{errMessage}</div> : ""}
        <button disabled={!formik.isValid} className='btn btn-info mt-2'>{loading ? <i className='fas fa-spinner fa-spin'></i> : "Register"}</button>
      </form>
    </div>
  </>
}
