import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import $ from "jquery"
import axios from 'axios'
export default function MovieDetails() {
  let {id , mediaType} = useParams()

  const [details, setdetails] = useState({})
  async function getTrending(id, mediaType) {
    let { data } = await axios.get(`https://api.themoviedb.org/3/${mediaType}/${id}?api_key=e7f41b81128088f4b634b539fcb837f8&language=en-US`)
    setdetails(data)
    console.log(data)
  }
  useEffect(() => {
    getTrending(id , mediaType)
    $(".Loading").fadeOut(2500)
  },[])
  return (
    <div className='row '>
      <div className="col-md-3">
      {details.poster_path? <img src={'https://image.tmdb.org/t/p/w500' + details.poster_path} className='w-100' alt="" />:<img src={'https://image.tmdb.org/t/p/w500' + details.profile_path} className='w-100' alt="" />}
      </div>
      <div className="col-md-7 d-flex align-items-center">
        <div>
        <h2 className=' mt-2 '>{details.title} {details.name}</h2>
<p className='text-muted my-3'>{details.overview} {details.biography}</p>
{details.vote_average? 
  <h4>vote average : {details.vote_average}</h4>
:""}
{details.vote_count? 
 <h4>vote count : {details.vote_count}</h4>
:""}
        </div>
      </div>
    </div>
  )
}
