import axios from "axios"
import { useEffect, useState } from "react"
import MediaType from "../MediaType/MediaType.jsx"
import $ from "jquery"
export default function Home() {
  const [movies, setMovies] = useState([])
  const [tv, setTv] = useState([])
  const [people, setPeople] = useState([])
  async function getTrending(mediaType, callbacks) {
    let { data } = await axios.get(`https://api.themoviedb.org/3/trending/${mediaType}/week?api_key=e7f41b81128088f4b634b539fcb837f8`)
    callbacks(data.results)
    $(".Loading").fadeOut(1000)
  }
  useEffect(() => {
    getTrending('movie', setMovies)
    getTrending('tv', setTv)
    getTrending('person', setPeople)
  }, [])
  return <>
  <div className='position-fixed top-0 end-0 start-0 bottom-0 bg-main Loading justify-content-center align-items-center'>
  <span className="loader"></span>
  </div>
    <div className="row py-3">
    <div className="Loading"></div>
      <div className="col-md-4 d-flex align-items-center">
        <div> 
          <div className="brdr w-25 mb-3"></div>
       <h2 className="h3">Trending <br />Movies <br />To Watch Now</h2>
       <p className="text-muted">Most Watched Movies By Week</p>
        <div className="brdr w-100 mt-3"></div>
        </div> 
      </div>
{movies.slice(0,10).map((item,index)=><MediaType key={index} item={item}/>)}
    </div>


    <div className="row py-3">
      <div className="col-md-4 d-flex align-items-center">
        <div> 
          <div className="brdr w-25 mb-3"></div>
       <h2 className="h3">Trending <br />tv <br />To Watch Now</h2>
       <p className="text-muted">Most Watched tv By Week</p>
        <div className="brdr w-100 mt-3"></div>
        </div> 
      </div>
{tv.slice(0,10).map((item,index)=><MediaType key={index} item={item}/>)}
    </div>

    <div className="row py-3 mb-5">
      <div className="col-md-4 d-flex align-items-center">
        <div> 
          <div className="brdr w-25 mb-3"></div>
       <h2 className="h3">Trending <br />people <br />To Watch Now</h2>
       <p className="text-muted">Most Watched people By Week</p>
        <div className="brdr w-100 mt-3"></div>
        </div> 
      </div>
{people.slice(0,10).map((item,index)=><MediaType key={index} item={item}/>)}
    </div>
  </>

}
