import React from 'react'
import axios from "axios"
import { useEffect, useState } from "react"
import $ from "jquery"
import { Link } from 'react-router-dom'
export default function Movies() {
  const [movies, setMovies] = useState([])
  let mediaType= "movie"
let nums = new Array(10).fill(1).map((elem,index)=>index+1)
  async function getTrending(page) {
    let { data } = await axios.get(`https://api.themoviedb.org/3/discover/movie?api_key=e7f41b81128088f4b634b539fcb837f8&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=true&page=${page}`
    )
    setMovies(data.results)
    $(".Loading").fadeOut(1000)
    console.log(data);
  }
  useEffect(() => {
    getTrending()
  }, [])
  return <>
    <div className='position-fixed top-0 end-0 start-0 bottom-0 bg-main Loading justify-content-center align-items-center'>
  <span className="loader"></span>
  </div>
    <div className="row">
      {movies.map((item, index) => {
        return <div key={index} className="col-md-3">
          <Link className='text-decoration-none text-white' to={`/moviedetails/${item.id}/${mediaType}`}>
            <div className='position-relative'>
              <img src={'https://image.tmdb.org/t/p/w500' + item.poster_path} className='w-100' alt="" />
              <h3 className='h6 mt-2 text-center'>{item.title}</h3>
              <div className="vote top-0 end-0 position-absolute p-1">
                {item.vote_average}
              </div>
            </div>
          </Link>
        </div>
      })}
    </div>
    <nav className='py-5'>
    <ul class="pagination pagination-sm d-flex justify-content-center">
    {nums.map((page)=>    <li key={page} onClick={()=>getTrending(page)} class="page-item mx-1" aria-current="page">
      <Link class="page-link">{page}</Link>
    </li>)}
  </ul>
    </nav>
  </>
}
